@import url(https://fonts.googleapis.com/css2?family=Allerta&family=Squada+One&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;box-sizing:border-box}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.App{margin:0 auto;position:absolute;top:0;bottom:0;left:0;right:0;background-color:#a27653;background-image:url(/static/media/danielle-macinnes-IuLgi9PWETU-unsplash.93320c36.jpg);background-size:cover;background-repeat:no-repeat;background-position:62% 0}@media only screen and (min-width: 768px){.App{background-position:center}}.App{display:flex;flex-direction:column}

#quote-box{display:flex;flex-direction:column;align-items:center;padding:0 1rem}.InnerBox{color:#f0fbfe;position:relative;display:flex;flex-direction:column;justify-content:space-between;align-items:center;margin-top:calc(8vh - 1rem);padding:calc(0.5vw + 1rem) calc(0.5vw + 0.5rem);height:45vh;background-color:#1e1916bd}.InnerBox__QuoteIcon{max-width:150px;width:calc(5vw + 20px)}.InnerBox__Text,.InnerBox__Author{min-width:50%;text-align:center}.InnerBox__Text{font-family:"Allerta", sans-serif;font-size:calc(0.5vw + 1.05rem);overflow-y:auto;z-index:0;line-height:1.5;letter-spacing:0.05rem}.InnerBox__Author{font-family:"Squada One", cursive;font-size:calc(0.5vw + 1.35rem);align-self:flex-end}.InnerBox__Hrzline{width:60%;height:0.01px;border-bottom:solid #f0fbfe 0.01px;background-color:#f0fbfe}.InnerBox__Text,.InnerBox__Author,.InnerBox__Hrzline{-webkit-animation:fade-in 1s forwards;animation:fade-in 1s forwards;transition:all ease-in 0.5s}@-webkit-keyframes fade-in{from{opacity:0}to{opacity:1}}@keyframes fade-in{from{opacity:0}to{opacity:1}}.InnerBox__LongTextIndicator{position:relative;transform:translateY(-35%);z-index:1;display:block;width:0;height:0;border-left:8px solid transparent;border-right:8px solid transparent;border-top:12px solid #91908d;-webkit-animation:scroll-down;animation:scroll-down;transition:all 0.5s ease;-webkit-animation-duration:1s;animation-duration:1s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite}@-webkit-keyframes scroll-down{0%{transform:translateY(-40%);opacity:0}25%{transform:translateY(-30%);opacity:0.25}50%{transform:translateY(-20%);opacity:0.75}75%{transform:translateY(-10%);opacity:1}100%{transform:translateY(-5%);opacity:0}}@keyframes scroll-down{0%{transform:translateY(-40%);opacity:0}25%{transform:translateY(-30%);opacity:0.25}50%{transform:translateY(-20%);opacity:0.75}75%{transform:translateY(-10%);opacity:1}100%{transform:translateY(-5%);opacity:0}}@media only screen and (min-width: 768px){.InnerBox{width:calc(100% - 4rem);max-width:1600px;display:grid;grid-gap:1.5rem 0;gap:1.5rem 0;grid-template-columns:auto 1fr auto;grid-template-areas:"QuoteIcon . ." "QuoteText QuoteText QuoteText" "ScrollInd ScrollInd ScrollInd" "Separator Separator Separator" ". . Author"}.InnerBox__QuoteIcon{grid-area:QuoteIcon;max-width:150px;width:calc(0.5vw + 100px);z-index:1;align-self:start}.InnerBox__Text{display:flex;align-items:center;justify-content:center;grid-area:QuoteText;z-index:1;overflow-y:auto;max-height:30vh}.InnerBox__Text #text{max-width:72ch}.InnerBox__LongTextIndicator{grid-area:ScrollInd;justify-self:center}.InnerBox__Hrzline{grid-area:Separator;width:50%}.InnerBox__Author{grid-area:Author;display:flex;flex-direction:column;justify-content:right;color:#281c1b;background-color:#91908d}.InnerBox__Author #author{padding:0.4rem 1rem}}

.Btns{padding:calc(0.5vw + 0.8rem) calc(0.5vw + 0.5rem);width:100%;display:grid;grid-template-columns:repeat(2, 1fr);grid-gap:0 1rem;gap:0 1rem}.Btns .btn{display:block;text-align:center;width:100%;padding:1rem;border-radius:0;border:none;cursor:pointer;text-transform:uppercase;--weblit-line-clamp: 1.5;transition:all 0.1s ease;color:#f0fbfe}.Btns .btn:hover{background-color:#f0fbfe;color:#281c1b}.Btns .btn--pr,.Btns .btn--sc{border:2px solid #f0fbfe}.Btns .btn--pr{background-color:rgba(40,28,27,0.85)}.Btns .btn--sc{background-color:rgba(162,118,83,0.85)}.Btns__BtnTweet #tweet-quote{text-decoration:none}@media only screen and (min-width: 768px){.Btns{width:45%}}

.Loading{display:block;position:absolute;top:50%;left:50%;transform:translate(-50%)}.Loading__lds-facebook{display:inline-block;position:relative;width:80px;height:80px;transition:all 0.5s ease-in-out;-webkit-animation:fade-in .5s forwards;animation:fade-in .5s forwards;opacity:0}@-webkit-keyframes fade-in{0%{opacity:0}100%{opacity:1}}@keyframes fade-in{0%{opacity:0}100%{opacity:1}}.Loading__lds-facebook div{display:inline-block;position:absolute;left:8px;width:16px;background:#91908d;-webkit-animation:lds-facebook 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;animation:lds-facebook 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite}.Loading__lds-facebook div:nth-child(1){left:8px;-webkit-animation-delay:-0.24s;animation-delay:-0.24s}.Loading__lds-facebook div:nth-child(2){left:32px;-webkit-animation-delay:-0.12s;animation-delay:-0.12s}.Loading__lds-facebook div:nth-child(3){left:56px;-webkit-animation-delay:0;animation-delay:0}@-webkit-keyframes lds-facebook{0%{top:8px;height:64px}50%,100%{top:24px;height:32px}}@keyframes lds-facebook{0%{top:8px;height:64px}50%,100%{top:24px;height:32px}}

footer{position:absolute;bottom:0;left:0;margin:0 auto;padding:calc(1vh + 0.4rem) 0;padding-bottom:0;width:100%;display:flex;justify-content:right}footer .PhotoCredit,footer .PhotoCredit__Link{color:#1e1916}footer .PhotoCredit{font-size:calc(0.2vw + 0.8rem);text-align:center;text-shadow:2px 2px 2px #9672588a}footer .PhotoCredit__Link{text-decoration:none;transition:color 0.2s ease}footer .PhotoCredit__Link:hover{text-decoration:underline}footer .FooterWrapper{padding:calc(0.5vw + 0.5rem);display:flex;justify-content:right;width:calc(100% - 4rem);max-width:400px}

